*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

ul {
  list-style: none;
}

body {
  height: 100vh;
}

.App {
  display: flex;
  flex-direction: column;
  height: 100vh;
}

main {
  flex: 1;
}

.header {
  text-align: center;
  padding: 20px 10px;
  background: antiquewhite;
}

.footer {
  text-align: center;
  padding: 20px 10px;
  background:aquamarine;
}

.item {
  display: flex;
  width: 100%;
  justify-content: space-between;
  align-items: center;
  padding: 15px 5px;
  margin-bottom: 15px;
  background: darkgray;
}

.item label { /*label doesn't have class. In this case the parent should be mentioned*/
  flex: 1;
  font-size: 19px;
  line-height: 20px;
  font-weight: 600;
}

.item input {
  width: 48px;
  height: 48px;
  cursor: pointer;
  margin-right: 5px;
}

.item svg {
    width: 48px;
    height: 48px;
    font-size: 16px;
    cursor: pointer;
    color: steelblue;
}

.item svg:focus,
.item svg:hover {
  color: red;
  outline: none;
}

.total-items {
  margin: 10px 0;
  font-size: 23px;
  font-weight: 700;
  color: blue;
}

.addForm {
  width: 100%;
  display: flex;
  padding: 15px 0;
  margin: 10px 0;
  border-bottom: 1px solid #eee;
}

.addForm input {
  flex: 1; /*Add item field stretches to the max width*/
  max-width: calc(100% - 50px); /*adds indentation of 50px from the right*/
  height: 48px;
  font-size: 16px;
  padding: 4px;
  outline: none;
}

.addForm button {
  height: 48px;
  border-radius: 5px;
  width: 48px;
  padding: 5px;
  background-color: aliceblue;
  color: mediumblue;
  cursor: pointer;
}

.addForm button:hover,
.addForm button:focus {
  color: white;
  background-color: lightgreen;
  outline: none;
}

.searchForm {
  width: 100%;
  display: flex;
  margin: 15px 0 0;
}

.searchForm input {
  flex: 1; /*Add item field stretches to the max width*/
  width: 100%;
  height: 48px;
  font-size: 16px;
  padding: 4px;
  outline: none;
}